* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
}

.m0Auto {
	margin: 0 auto;
}
.justifyCenter .MuiTabs-flexContainer {
	justify-content: center;
}
.MuiFormControl-root {
	width: 100%;
}
.text-center {
	text-align: center;
}
.btnSubmit button{
	margin-top: 20px;
}
.simpleMultiSelect .MuiOutlinedInput-notchedOutline {
	border-right: 0;
    border-left: 0;
    border-top: 0;
    border-radius: 0;
}
.simpleMultiSelect label {
	    left: -15px;
		top: -5px;
}

.simpleMultiSelect .MuiSelect-select {
	padding-top: 9px;
    padding-bottom: 8px;
	padding-left: 0;
}
.simpleMultiSelect .MuiFormControl-root {
	margin-left: 0;
	margin-right: 0;
}
.mb35 {
	margin-bottom: 35px;
}
.ml0 {
	margin-left: 0 !important;
}

.mt30 {
	margin-top: 30px;
}
h4 p {
	font-weight: 500;
	display: inline-block;
}